// header,
// footer,
// ul,
// ol,
// nav
@mixin block-children($dir, $value) {
  &.-#{$dir} {
    align-items: $value !important;
  }
  & > ._#{$dir} {
    align-self: $value !important;
  }
}
.block {
  display: flex;
  flex-flow: column;
  @include block-children(r, end);
  @include block-children(l, start);
  @include block-children(c, center);
  @include block-children(s, stretch);
  &.t {
    justify-content: start !important;
  }
  &.b {
    justify-content: end !important;
  }
  &.y {
    justify-content: center !important;
  }
  &.x {
    align-items: center !important;
  }
  &.c {
    align-items: center !important;
    justify-content: center !important;
  }
  & > ._c {
    align-self: center !important;
  }
  &.s {
    justify-content: stretch !important;
  }
  & > ._s {
    align-self: stretch !important;
  }
}

@mixin el-children($dir, $value) {
  &.-#{$dir} {
    text-align: $value !important;
  }
}
.el {
  @include el-children(l, left);
  @include el-children(r, right);
  @include el-children(c, center);
  &.c {
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: center;
  }
  &.x {
    display: flex;
    flex-flow: row;
    justify-content: center;
  }
  &.y {
    display: flex;
    flex-flow: row;
    align-items: center;
  }
  &.t {
    display: flex;
    flex-flow: row;
    align-items: start;
  }
  &.b {
    display: flex;
    flex-flow: row;
    align-items: end;
  }
  &.l {
    display: flex;
    flex-flow: row;
    justify-content: start;
  }
  &.r {
    display: flex;
    flex-flow: row;
    justify-content: end;
  }
  &.sb {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
  }
  &.sa {
    display: flex;
    flex-flow: row;
    justify-content: space-around;
  }
  &.se {
    display: flex;
    flex-flow: row;
    justify-content: space-evenly;
  }
}

