/* ==========================================================================
  Stylesheet for FrontierJS
============================================================================= */

/* Settings: preprocessor variables, colors, global settings
============================================================================= */
@import 's-color-palette';
@import 's-variables'; //This needs work
@import 's-breakpoints'; //This needs clean up

/* Generic/Elements: element selectors
============================================================================= */
@import 'e-mixins'; //Don't need to touch unless adding a new mixin
@import 'e-reset'; //Don't touch, just default reset css
@import 'e-base'; // Fine tune this one as a solid base for new project

/* Utilities: specific helper classes
============================================================================= */
@import 'u-containers';
@import 'u-text';
@import 'u-generic';

/* Components: separately styled components (buttons, form inputs, toolbars...)
============================================================================= */
@import 'c-form-input';
@import 'c-button';

