*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  font-family: $d-font-family;
  font-size: $d-font-size;
  line-height: $d-line-height;
  color: #333;
  background: #fff;
  font-feature-settings: 'liga', 'kern';
}

form {
  display: block;
}

img,
video {
  max-width: 100%;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
}

em,
i {
  font-style: italic;
}

strong,
b {
  font-weight: $d-headings-font-weight;
}

abbr[title] {
  text-decoration: none;
  opacity: 0.5;
}

img {
  max-width: 100%;
}

main {
  height: $d-main-height;
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a,
.link {
  font-weight: normal;
  padding: $d-link-padding;
  background-color: transparent;
  text-decoration: none;
  color: $d-link-color;
  &:hover {
    background-color: transparent;
    color: $d-link-hover-color;
  }
}

small {
  font-size: 0.825em;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-weight: 600;
  line-height: $d-line-height;
}

h1,
.h1 {
  font-size: 2.5rem;
}

h2,
.h2 {
  font-size: 2rem;
}

h3,
.h3 {
  font-size: 1.75rem;
}

h4,
.h4 {
  font-size: 1rem;
}

h5,
.h5 {
  font-size: 0.825rem;
}

h6,
.h6 {
  font-size: 0.825rem;
  display: inline-block;
}

