/* 12px @ 320px increasing to 18px @ 1920px */
@media (min-width: 300px) {
  :root {
    font-size: calc(0.75rem + ((1vw - 3.2px) * 0.375));
    /* Where: * 0.375 = 100 * font_Size_Difference / viewport_Width_Difference */
  }
}
/* Prevent font scaling beyond this breakpoint */
@media (min-width: 1920px) {
  :root {
    font-size: 18px;
  }
}

@mixin elements-debug($el, $color) {
  .#{$el} {
    background-color: $color;
    border: 1px solid darken($color, 10%);
    & > * {
      background-color: lighten($color, 20%);
    }
  }
}
@mixin el-debug() {
  @include elements-debug(grid, green);
  @include elements-debug(box, #3232cc);
  @include elements-debug(block, #b22222);
  @include elements-debug(el, #b0b045);
}

@include el-debug;
