/* Grid
============================================================================= */
$g-keywords: center, start, end, stretch, baseline;
$g-properties: (
  content: 'c',
  items: 'i',
  self: 's',
);
$g-axises: (
  place: '',
  justify: 'x',
  align: 'y',
);

@each $keyword in $g-keywords {
  @each $pkey, $prop in $g-properties {
    @each $akey, $axis in $g-axises {
      .g-#{$prop}#{$axis}-#{$keyword} {
        #{$akey}-#{$pkey}: $keyword;
      }
    }
  }
}

.grid {
  display: grid;
  grid-auto-flow: row;
  &.columns {
    grid-auto-flow: column;
  }
  &.g-flow-column {
    grid-auto-flow: column;
  }
  &.s {
    place-content: stretch;
  }
  &.sb {
    place-content: space-between;
  }
  &.sa {
    place-content: space-around;
  }
  &.se {
    place-content: space-evenly;
  }
  &.c {
    place-content: center;
  }
  &.b {
    align-content: end;
  }
  &.b {
    align-items: end;
  }
  &.r {
    place-content: end;
  }
  &.-r {
    place-items: end;
  }
  &.x {
    justify-content: center;
  }
  &.y {
    align-content: center;
  }
  &.-c {
    place-items: center;
  }
  &.-x {
    justify-items: center;
  }
  &.-y {
    align-items: center;
  }
  & > * {
    &._c {
      place-self: center;
    }
    &._x {
      justify-self: center;
    }
    &._y {
      align-self: center;
    }
    &._l {
      justify-self: start;
    }
    &._r {
      justify-self: end;
    }
    &._b {
      align-self: end;
    }
  }
}
.-gap {
  gap: 10vmin;
}
.-gap-sm {
  gap: 5vmin;
}
.-gap-lg {
  gap: 20vmin;
}

.grid-all > * {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(16px, 1fr));
}

/* Flex
============================================================================= */
@mixin flex() {
  $direction: column;
  .flex {
    display: flex;
    flex-flow: $direction wrap;
    justify-content: center;
    align-items: center;
    &.grid-all > * {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax($block, 1fr));
    }
    &.flex-start {
      justify-content: flex-start;
    }
    &.space-between {
      justify-content: space-between;
    }
    &.space-around {
      justify-content: space-around;
    }
    &.align-start {
      align-items: start;
    }
    &.align-end {
      align-items: end;
    }
    &.row {
      $direction: row;
      flex-direction: $direction;
      &.fx-stretch-all {
        > * {
          flex: 1 1 auto;
        }
      }
      &.fy-stretch-all {
        align-items: stretch;
      }
    }
    &.no-wrap {
      flex-wrap: nowrap;
    }
    &.stretch-all {
      align-items: stretch;
    }
    &.grow-all > * {
      flex: 1 1 auto;
    }
    &:not(.row).fx-stretch-all {
      align-items: stretch;
    }
    &:not(.row).fy-stretch-all > * {
      flex: 1 1 auto;
    }
    //Alias for stretch and grow
    &.fill-all {
      align-items: stretch;
      > * {
        flex: 1 1 auto;
      }
    }
    //This section and nav overwrite might be an anit-pattern
    & > section,
    & > nav {
      align-self: stretch;
    }
    & > * {
      &.baseline {
        align-self: baseline;
      }

      &.stretch {
        align-self: stretch;
      }
      &.grow {
        flex-grow: 1;
      }
      //Alias for stretch and grow
      &.fill {
        align-self: stretch;
        flex-grow: 1;
      }
    }
  }
}

//Containers
/* Sizing
============================================================================= */
.h-full {
  height: 100%;
}
.h-screen {
  height: 100vh;
}

// Width
.w-full {
  width: 100%;
}
.w-screen {
  width: 100vw;
}

.w-max-xxs {
  max-width: $xxs;
}
.w-max-xs {
  max-width: $xs;
}
.w-max-sm {
  max-width: $sm;
}
.w-max-md {
  max-width: $md;
}
.w-max-lg {
  max-width: $lg;
}

// Floats
.float-left {
  float: left;
}
.float-right {
  float: right;
}

